/* Basic styling for all devices */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f0f0f0;
}

.container {
    width: 90%;
    max-width: 1200px;
    margin: 20px auto;
    background-color: white;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1, h2 {
    color: #333;
}

.profile-pic {
    width: 150px;
    border-radius: 50%;
    margin-bottom: 15px;
}

button {
    padding: 10px 20px;
    margin-top: 20px;
    background-color: #8a1095;
    color: white;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #d931d1;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .container {
        width: 95%;
    }

    .profile-pic {
        width: 120px;
    }

    h1 {
        font-size: 28px;
    }

    h2 {
        font-size: 22px;
    }
}

.profile-pic{
    width: 200px;
    height: 100px;
    border-radius: 0px;
}
